.sideNav {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    position: fixed;
    background-color: white;
    height: 100vh;
    width: 250px;
    padding: 100px 0 0 0;
    margin: 0 0 0 -260px;
    z-index: 100;
    list-style-type: none;
    -webkit-transition: .2s all ease-in-out;
    -o-transition: .2s all ease-in-out;
    transition: .2s all ease-in-out;
    -webkit-box-shadow: 10px 0px 16px -8px rgba(0, 0, 0, 0.75);
    box-shadow: 10px 0px 16px -8px rgba(0, 0, 0, 0.75);
}

.sideNav li {
    padding-bottom: 40px;
}

.sideNav li a {
    position: relative;
    overflow: hidden;
    color: #3498db;
    text-decoration: none;
    font-weight: 400;
    font-size: 1.3em;
    padding-left: 40px;
    -webkit-transition: .2s all ease-in;
    -o-transition: .2s all ease-in;
    transition: .2s all ease-in;
}

.sideNav li a:hover {
    color: #B0BEC5;
    margin-left: 15px;
}

.visible {
    margin: 0;
}

.sideNav .material-icons:not(.material-more-options-icon) {
    margin-right: 20px;
    vertical-align: bottom;
}

.material-face-icon {
    color: #009688;
}

.material-assignment-icon {
    color: #F44336;
}

.material-email-icon {
    color: #FFEB3B;
}

.material-home-icon {
    color: #673AB7;
}

.material-hamburger-icon {
    color: white;
    position: fixed;
    border-radius: 50%;
    top: 10px;
    right: 10px;
    cursor: pointer;
    background-color: #E91E63;
    padding: 10px;
    -webkit-box-shadow: 8px 3px 33px -1px rgba(0, 0, 0, 0.75);
    box-shadow: 8px 3px 33px -1px rgba(0, 0, 0, 0.75);
}

.material-hamburger-icon:after {
    content: '';
    width: 1em;
    height: 1em;
    opacity: 0;
    position: absolute;
    background: rgba(0, 0, 0, .5);
    border-radius: 50%;
    -webkit-animation: menu 2s infinite;
            animation: menu 2s infinite;
    left: 0px;
    top: 0px;
    right: 0px;
    bottom: 0px;
    margin: auto;
    pointer-events: none;
}

.material-hamburger-icon.delay-1:after {
    -webkit-animation-delay: 1s;
            animation-delay: 1s
}

.material-hamburger-icon.delay-2:after {
    -webkit-animation-delay: 1s;
            animation-delay: 1s
}

@-webkit-keyframes menu {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 1;
    }
    40% {
        -webkit-transform: scale(2);
                transform: scale(2);
        opacity: 0;
    }
    100% {
        -webkit-transform: scale(4);
                transform: scale(4);
        opacity: 0
    }
}

@keyframes menu {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 1;
    }
    40% {
        -webkit-transform: scale(2);
                transform: scale(2);
        opacity: 0;
    }
    100% {
        -webkit-transform: scale(4);
                transform: scale(4);
        opacity: 0
    }
}

.material-icons.md-36 {
    font-size: 36px;
}

h1 {
    text-align: center;
    margin-top: 0;
    color: white;
}

.loader {
    border: 8px solid #ccc;
    border-top: 8px solid white;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    margin: 0 auto;
    position: relative;
    -webkit-animation: spin 1s linear infinite;
            animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.avatar {
    height: 100vh;
    background-image: url('background.jpg');
    background-color: #3498db;
    background-color: white;
    background-size: cover;
    display: -ms-flexbox;
    display: flex;
    padding: 0 20%;
    color: white;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    text-align: center;
    -ms-flex-direction: column;
        flex-direction: column;
}

.avatar__picture {
    border-radius: 50%;
    padding: 10px;
    border: 2px solid #ccc;
    -webkit-transition: .2s all linear;
    -o-transition: .2s all linear;
    transition: .2s all linear;
}

.avatar__picture:hover {
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
}

.avatar__name {
    font-size: 3em;
    margin: 0;
}

.avatar__position {
    margin: 0;
    color: #ccc;
}

.avatar__text {
    font-size: 1.2em;
}

.footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 0.9em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.footer__text {
    color: #CFD8DC;
    margin: 0;
}

@media screen and (max-width: 600px) {
    .sideNav {
        width: 150px;
        padding: 60px 0 0 0;
        margin: 0 0 0 -160px;
    }

    .sideNav li a {
        padding: 10px 10px;
        font-size: 0.9em;
    }

    .visible {
        margin: 0;
    }

    .material-icons.md-24 {
        font-size: 24px;
    }

    h1 {
        font-size: 1.2em;
    }

    .avatar {
        padding: 0 10%;
    }

    .avatar__name {
        font-size: 2em;
    }

    .avatar__picture {
        width: 140px;
        height: auto;
    }

    .avatar__text {
        font-size: 0.8em;
    }

    .footer {
        font-size: 0.6em;
    }
}


